.project-preview {
  margin: 50px auto;
  text-align: center;
  position: relative;
  font-family: 'Montserrat', sans-serif;
  .small-title {
    font-size: 2.5rem;

    @media (max-width: 800px) {
      font-size: 1.5rem;
    }

  }
  img {
    // flex-basis: 50%;
    max-width: 100%;
    // border-radius: 35px;

  }

  .project-logo {
    // background-repeat: no-repeat;
    background-size: contain;
    width: 250px;
  }
  
  .project-skills {
    padding-block-end: 30px;
  }
  
  .project-actions {
    display: flex;
    gap: 15px;
    transition: all 0.8s ease;
    justify-content: center;
    padding: 0;
    margin-block-start: 1.5rem;
    position: relative;
    right: 10px;
    // transform: translateX(-50%);
    button {
      display: flex;
      justify-content: center;
    }
    .wrapper {
      position: relative;
      bottom: 50px;
      display: flex;
      gap: 8px;
      
      @media(max-width: 600px) {
        flex-direction: column;
        bottom: 320px;
      }
    }
  }
  @media (max-width: 1100px) {
    text-align: center;
    gap: 15px;

  }
  
  &:last-child {
    border-bottom: none;
  }
  
  &:first-child {
    padding-top: 0;
  }
  
}

.container-2 {
  min-height: 450px;
  max-width: 650px;
  margin: 0 auto;
  background-color: #f1f1f1;
  padding: 1.3rem;
  border: 1px solid #ccc;
  border-radius: 100px 20px 0 20px;
  text-align: center;
  overflow: hidden;
  transition: 0.6s;
  color: black;

  &:hover {
    color: #fff;
  }
  &:hover {
    .number {
      box-shadow: 0 0 0 500px #9e394d;
    }
  }
  .number {
    margin: 20px auto;
    width: 60px;
    height: 60px;
    background-color: #9e394d;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;

    font: {
      size: 24px;
      weight: bold;
      
    }
    

    border-radius: 50%;
    transition: 0.7s;
  }
  h2 {
    margin: 20px 0;
    padding: 10px;
    font-size: 1.8em;
    text-transform: uppercase;
  }
  .text {
    line-height: 1.7;
    font: {
      // style: italic;
      size: 18px;
    }
    @media(max-width: 530px) {
      font-size: 14px;
    }
  }
}
