.projects-container {
  color: #f5f5f5;
  margin-block-start: 2rem;
  min-height: 100vh;
  padding-block: 2rem;

  .blob-bg {
    min-height: 750px;
    min-width: 550px;
    position: absolute;
    // top: 50%;
    // left: 50%;
    transform: translate(45%, -90%);
    // filter: invert(50%) sepia(200%) saturate(250%) hue-rotate(298deg)
    //   brightness(50%) contrast(100%);

    z-index: -1;
    color: #f5f5f5;
  }

  @media (max-width: 1100px) {
    margin-block-start: 0.5rem;
  }

  @media (max-width: 600px) {
    margin-block-start: 1rem;
  }

  .project-skills {
    padding: 1rem;
    display: flex;
    align-items: center;

    .skill-list {
      gap: 5px;
    }
  }

  .projects-body {
    width: 100%;
    @media (max-width: 1100px) {
      border: none;
      outline: none;
      background-color: transparent;
    }

    @media (max-width: 970px) {
      grid-template-columns: minmax(300px, 500px);
    }
  }
  .hide {
    opacity: 0;
    transition: opacity 0.6 ease;
  }
}
