.hero-wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  cursor: default;
  @media (max-width: 900px) {
    height: 110vh;
  }
  canvas {
    height: 500px;
    width: 500px;

    @media(max-width: 1300px) {
      max-width: 420px;
      max-height: 420px;
    }
  }

  @media (max-width: 900px) {
    flex-direction: column;
  }

  @media (max-width: 740px) {
    .canvas-image {
      max-width: 350px;
      // position: relative;
    }
  }
  @media (max-width: 650px) {
    .canvas-image {
      max-width: 300px;
      position: relative;
    }
  }
  align-items: center;
}

.hero-container {
  font-family: 'Staatliches', sans-serif;
  color: black;
  // padding: 1rem;
  position: relative;

  @media (max-width: 1500px) {
    padding: 0;
  }

  .blob-bg {
    min-height: 1000px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-65%, -50%);
    z-index: -1;
  }
  .hero-title {
    // display: flex;
    justify-content: center;
    // transition: opacity 1s linear, transform 1s ease-in-out;
    font-size: 5rem;
    // text-align: center;
    line-height: 75px;
    color: transparent;
    -webkit-text-stroke: 1px white;
    span.purple {
      position: relative;
      // -webkit-text-stroke: 0px;
    }

    @media (max-width: 1600px) {
      font-size: 4.5rem;
      line-height: 65px;
    }

    @media (max-width: 600px) {
      line-height: 45px;
      font-size: 3rem;
      justify-content: center;
    }
    @media (max-width: 400px) {
      justify-content: center;
      text-align: center;
    }
  }

  .hero-details {
    font-size: 1.5rem;
    font-family: 'Lato';
    display: flex;
    flex-direction: column;
    gap: 10px;
    -webkit-text-stroke: 0px;
    color: #f5f5f5;
    .mail-link {
      width: fit-content;

      @media(max-width: 500px) {
        align-self: center;

      }
    }

    span {
      font-size: 2rem;
      line-height: 50px;
      @media (max-width: 800px) {
        font-size: 1.5rem;
      }

      @media (max-width: 500px) {
        font-size: 1rem;
      }
    }
  }
}

.arrow-wrapper {
  position: absolute;
  z-index: -1;
  max-width: 250px;
  filter: invert(100%) sepia(5%) saturate(7449%) hue-rotate(120deg) brightness(113%) contrast(121%);
  transform: translate(-50%, 20%);
  // right: 350px;
  bottom: -20%;
  opacity: 0.35;
  .arrow-img {
    position: relative;
    opacity: 0.75;
    transform: rotate(180deg);
    bottom: 400px;
    max-width: 250px;
  }


  span {
    position: relative;
    font-family: 'Sue Ellen Francisco';
    font-size: 3rem;
    bottom: 80px;
  }
  @media(max-width: 1550px) {
    // max-width: 200px;
    // bottom: -150px;
    // transform: translateY(150px);
  }
 @media(max-width: 1000px) {
  display: none;
 }
}

