// List
.clean-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  & > li {
    margin: 0;
    padding: 0;
  }
}

// Buttons

.nice-button {
  border: none;
  padding: 15px;
  font-size: 1.5rem;
  background: transparent;
  border-radius: 30px;
  font-family: 'Lato';
  text-transform: capitalize;
  transition: all 0.1s;
  color: whitesmoke;
  outline: 2px solid #ef6e79;
  transform: perspective(1px) translateZ(0);
  transition: 0.4s;
  overflow: hidden;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    top: 0px;
    left: 0px;
    width: 0px;
    height: 100%;
    background-color: #ef6e79;
    transition: all 0.6s ease;
    border-radius: 30px;
    z-index: -1;
  }

  &:hover::before {
    width: 100%;
    opacity: 0.6;
  }
  &:hover {
    box-shadow: 0 0 0 10px rgba(250, 250, 250, 0.1);
  }
  @media (max-width: 450px) {
    font-size: 1.2rem;
    padding: 7px;
  }
  @media (min-width: 1100px) {
    padding-block: 20px;
    padding-inline: 35px;
  }
}
.simple-button {
  background-color: #f3f3f3;
  font-family: 'Montserrat', sans-serif;
  color: black;
  font-size: inherit;
  text-transform: capitalize;
  border: 0;
  transition: 0.3s all;
  border-radius: 0 0 10px 0;
  box-shadow: 1px 1px 3px rgba(40, 40, 40, 0.714);
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  padding: 7px;
  cursor: pointer;
  &:hover {
    background: rgb(238, 238, 238);
    outline: 1px solid rgb(0, 0, 0);
    color: rgb(18, 16, 16);
    transform: rotate(3.5deg);
  }
  &:disabled {
    cursor: initial;
    opacity: 0.2;
  }

  &:hover:not(:disabled) {
    opacity: 1;
  }
  @media (max-width: 450px) {
    font-size: 0.75rem;
  }
}

.purple {
  // color: #ff6f8c;
  -webkit-text-stroke: 1px #ffb9c7;
}

.page-headline {
  text-transform: uppercase;
  text-align: center;
  margin-block: 2rem;
  text-shadow: 2px 2px 1px #801f35;
  font-size: 3.5rem;
  &::after {
    content: '';
    height: 10px;
    width: 40px;
    position: absolute;
    background: linear-gradient(to left, #661528, transparent);
    // top: 5px;
    border-radius: 50px;
    transform: translateY(3rem);

    @media (max-width: 600px) {
      transform: translateY(1.8rem);
    }
  }

  @media (max-width: 600px) {
    font-size: 2rem;
  }
}

.small-title {
  // color: #dadee4;
  font-size: 0.8571428571rem;
  font-weight: 600;
  letter-spacing: 0.04em;
  font-size: 0.8rem;
  text-transform: uppercase;
  &.smaller {
    font-size: 0.7rem;
  }
}

.svg-small {
  max-width: 40px;
}

.svg-medium {
  max-width: 60px;
}

//   TOOLTIP

.tooltip {
  position: relative;
  display: inline-block;
  font-family: 'Lato', cursive;
  font-size: 0.8rem;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgba(29, 28, 28, 0.897);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
  left: 60px;
  top: -5px;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.move {
  transform: translateY(20vh);
}

.inline-block {
  display: inline-block;
}
.pos-absolute {
  position: absolute;
}

.blast {
  display: inline-block;
}

.bounce-1 {
  &:hover {
    animation-name: bounce-1;
    animation-timing-function: cubic-bezier(0.28, 0.32, 0.42, 1);
    animation-duration: 1.2s;
    animation-iteration-count: infinite;
  }
  // animation-delay: 5s;
}
.bounce-2 {
  &:hover {
    animation-name: bounce-2;
    animation-timing-function: cubic-bezier(0.28, 0.32, 0.42, 1);
    animation-duration: 0.9s;
    animation-iteration-count: infinite;
  }
}

.bounce-3 {
  &:hover {
    animation-name: bounce-3;
    animation-timing-function: cubic-bezier(0.28, 0.32, 0.42, 1);
    animation-duration: 1s;
    animation-iteration-count: infinite;
  }
}

.font-inherint {
  font-family: 'Montserrat';
