@keyframes slide-in-out {
    0% {
        transform: translateY(150%);
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes scalein {
    from {
        transform: scale(0.6);
    }

    to {
        transform: scale(1);
    }
}

@keyframes scaleout {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.6);
    }
}

@keyframes scaleinsoft {
    from {
        transform: scale(0.8);
    }

    to {
        transform: scale(1);
    }
}


.blobs {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    margin-top: 1rem;
    .blob {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        width: 250px;

    }
}
.blob:nth-child(2) {
    filter: blur(20px);
}

@keyframes bounce-1 {
    0% {
      transform: scale(1, 1) translateY(0);
    }
    10% {
      transform: scale(1.1, 0.9) translateY(0);
    }
    30% {
      transform: scale(0.9, 1.1) translateY(-7px);
    }
    50% {
      transform: scale(1.05, 0.95) translateY(0);
    }
    57% {
      transform: scale(1, 1) translateY(0);
    }
    64% {
      transform: scale(1, 1) translateY(0);
    }
    100% {
      transform: scale(1, 1) translateY(0);
    }
  }

  @keyframes bounce-2 {
    0% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    10% {
      transform: scale(1.1, 0.9) translateY(0);
    }
    30% {
      transform: scale(0.9, 1.1) translateY(-7px) translateX(-3px);
    }
    50% {
      transform: scale(1.05, 0.95) translateY(0) translateX(0);
    }
    57% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    64% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    100% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
  }
  @keyframes bounce-3 {
    0% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    10% {
      transform: scale(1.1, 0.9) translateY(0);
    }
    30% {
      transform: scale(0.9, 1.1) translateY(-7px) translateX(3px);
    }
    50% {
      transform: scale(1.05, 0.95) translateY(0) translateX(0);
    }
    57% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    64% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
    100% {
      transform: scale(1, 1) translateY(0) translateX(0);
    }
  }


  .slide-fade-off {
    transition: all .6s ease;
    transform: translateY(30%);
    opacity: 0;
  }
  
  .slide-fade-on {
    // animation: slide-in-out 1s, fadein 1s;
    transition: all .6s ease;
    transform: translateY(0);
    transition-delay: .3s;
    opacity: 1;
  }