.header-container {
  background-color: lighten(#0f0909, 5%);
  border-right: 1.5px solid rgba(245, 245, 245, 0.13);
  // width: 45px;
  padding-inline: 0.75rem;
  z-index: 100;
  max-height: 100vh;
  transition: transform 0.1s;
  backdrop-filter: blur(3px);
  position: fixed;
  left: 0%;
  top: 0%;
  right: auto;
  bottom: 0%;
  height: 100%;

  @media(max-width: 350px) {
    padding-inline: 0.4rem;
  }

  .hover-bg {
    height: 100%;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.026);
    z-index: -1;
    position: absolute;
    top: 0;
    transform: translateX(-101%);
    transition: transform .5s;
  }

  &:hover {
    .hover-bg {
      transform: translateX(0);
    }
  }
  
  nav {
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    user-select: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    ul {
      gap: 25px;
      flex-direction: column;
    }
    .nav-link {
      transition: 0.3s all;
      text-transform: uppercase;
      position: relative;
      padding-block-end: 3px;
      font-size: 1.2rem;
      font-weight: 100;
      color: whitesmoke;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;

      &.active {
        transition: all .4s ease;
        .nav-icon {
          transition: all .4s ease;
          filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(117deg) brightness(102%) contrast(101%);
          color: #fff;
        }
      }
      @media (max-width: 600px) {
        padding-block-end: 0;
        margin-block-end: 1rem;
      }

      p {
        font-size: 0.75rem;
        letter-spacing: 3px;

        @media(max-width: 450px) {
          letter-spacing: 1px;
        }
      }
      .nav-icon {
        height: 25px;
        width: 25px;
        filter: invert(88%) sepia(31%) saturate(2%) hue-rotate(256deg) brightness(70%) contrast(100%);
      }
    }

    li {
      gap: 1.5px;
      // background: linear-gradient(to bottom, #661528, #490112);
      width: 100%;
    }
  }
}
